.qp-nav-links {
    @apply hidden absolute top-1/2 left-1/2 
        transform -translate-y-1/2 -translate-x-1/2 
        lg:mx-auto 
        lg:flex lg:items-center lg:w-auto 
        lg:space-x-8;
}

a.qp-nav-link {
    @apply hover:text-darkred;
}
.qp-nav-buttons {
    @apply hidden 
        lg:flex lg:justify-between lg:space-x-6;
}

.qp-nav-btn {
    @apply lg:inline-block
        bg-darkblue
        hover:bg-darkred 
        focus:bg-darkred focus:outline-none focus:ring-0 
        active:bg-darkred motion-reduce:transition-none
        px-3 py-2        
        text-base font-medium leading-normal text-white
        transition duration-150 ease-in-out;
}

.qp-navbar-backdrop {
    @apply fixed 
        inset-0 
        bg-gray-800 
        opacity-50;
}

.qp-mobile-nav-slider {
    @apply fixed 
        flex flex-col justify-start
        bg-white 
        top-0 left-0 bottom-0      
        w-5/6 max-w-sm 
        py-2 px-8
        border-r
        overflow-y-auto;
}

.qp-mobile-nav-header {
    @apply flex items-center justify-between 
        mb-8;
}

.qp-mobile-nav-links {
    @apply flex items-center justify-start
        mb-8;
}

.qp-mobile-nav-links > ul {
    @apply lg:hidden
        flex flex-col
        space-y-6;
}

.qp-mobile-nav-footer {
    @apply flex items-center justify-start
        space-x-6 
        mb-8;
}
