/* 
 * CSS - Default Theme
 */

@import "notifications.css";

@import "nav.css";
@import "header.css";

@import "form.css";
@import "card.css";
@import "homepage.css";
@import "dashboard.css";

@import "blog.css";
@import "page.css";

html,
body {
    @apply font-poppins;
}

p {
    @apply pb-4;
}

/* Full Width Container - Master Layout */
.qp-fullwidth-container {
    @apply w-full;
}

/* Container To Center Page Content */
.qp-container {
    @apply container mx-auto
        px-4;
}

/* Content Wrap */
.qp-content-wrap {
    @apply w-full
        mx-auto
        py-8;
}

/* Page Header */
.qp-page-header {
    @apply w-full
        mb-8;
}

/* Page Section */
.qp-page-section {
    @apply w-full
        my-4 md:mt-8 md:mb-12;
}

.qp-page-section > h1 {
    @apply font-bold 
        text-xl md:text-3xl
        text-darkblue
        mb-4;
}

.qp-page-section > h2 {
    @apply font-bold 
        text-lg md:text-2xl
        text-darkblue
        mb-4;
}

.qp-page-section > p {
    @apply font-medium 
        text-sm md:text-lg
        mb-4;
}

.qp-page-section > a,
.qp-page-section > p > a {
    @apply text-darkred hover:text-darkblue;
}

/* Profile */
.qp-tabs-container {
    @apply flex items-start justify-start
    w-full h-screen;
}

/* Footer */
.qp-footer-container {
    @apply bg-darkblue;
}

.qp-footer {
    @apply py-8
        text-white text-sm;
}

.qp-footer > p {
    @apply p-0;
}

.qp-footer a {
    @apply text-darkred;
}
